* {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1rem;
}

body {
  background: #0e0e0e;
}

.grid-container {
  grid: auto auto auto / auto;
  display: grid;
}

a {
  color: #fff;
  text-decoration: none;
}

header {
  grid-row: 1;
}

header nav {
  flex-wrap: nowrap;
  display: flex;
}

header nav ul li {
  height: 100%;
}

header nav ul li a {
  padding: 1rem;
}

header nav form {
  grid-template: auto / auto 70% 20%;
  display: grid;
}

header nav form input {
  padding: 1rem;
}

header nav form select {
  color: #fff;
  background: #6e6e6e80;
  border: 1px solid #0000;
  border-start-start-radius: .5rem;
  border-end-start-radius: .5rem;
  padding: 1rem;
}

header nav form .form-content {
  background: #6e6e6e80;
  border: 1px solid #0000;
}

header nav form .form-submit {
  color: #fff;
  background: #6e6e6e80;
  border: 1px solid #0000;
  border-start-end-radius: .5rem;
  border-end-end-radius: .5rem;
}

main {
  background: #0e0e0e;
  grid-row: 2;
}

main .banner {
  height: 20vh;
  background: #0e0e0e;
}

main .banner p {
  color: #fff;
  text-align: center;
  padding: 2rem;
  font-size: 2rem;
}

main .article-form {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

main .article-form input {
  color: #fff;
  background: #6e6e6e80;
  border: 1px solid #0000;
  border-start-start-radius: .5rem;
  border-end-start-radius: .5rem;
  padding: 1rem;
}

main .article-form a {
  background: #6e6e6e80;
  border: 1px solid #0000;
  border-start-end-radius: .5rem;
  border-end-end-radius: .5rem;
  padding: 1rem;
  font-size: .9rem;
}

main .article-msg article {
  background: #22222280;
  border: 1px solid #000;
  border-radius: .5rem;
  grid-template: auto / auto auto 70% auto;
  margin: 1rem;
  display: grid;
}

main .article-msg article p {
  color: #fff;
  padding: 1rem;
}

main .article-msg article button {
  grid-column: 4;
}

main .article-msg article form {
  grid-column: 1 / 4;
}

main .article-msg article .msg-content {
  grid-area: 1 / 3;
}

footer {
  grid-row: 3;
}

@media (min-width: 500px) {
  main .banner {
    height: 60vh;
    background: url("1024x800.3df5abf8.png") top / cover;
  }
}

@media (min-width: 1300px) {
  main .banner {
    height: 60vh;
    background: url("1920x1200.d9cca484.png") top / cover;
  }
}

@media (min-width: 1920px) {
  main .banner {
    height: 60vh;
    background: url("2560x1600.1e8cfc60.png") top / cover;
  }
}

